:root {
  --color-primary: rgba(6, 84, 103, 0.714);
  --color-secondary: #e9e8e4;
}

body {
  font-family: Arial, sans-serif;
  background-image: url("../img/background-criadores.png");
  margin: 0;
  padding: 0;
}

header {
  background-color: var(--color-primary);
  color: #fff;
  padding: 20px;
  opacity: 100%;
}

header h1 {
  margin: 0;
  font-size: 36px;
}

nav ul {
  list-style: none;
  margin: 0;
  padding: 0px 0px 10px 0px;
  display: flex;
}

nav li {
  margin-right: 20px;
}

nav a {
  color: #fff;
  text-decoration: none;
  font-weight: bold;
}

nav a:hover {
  text-decoration: underline;
}

.name {
  display: flex;
  padding-bottom: 10px;
}

.name-team {
  padding-left: 10px;
}

/* Texto */

h1 {
  font-family: Arial, Helvetica, sans-serif;
  text-align: center;
}

h2 {
  font-size: 40px;
  font-weight: 400;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  text-align: center;
  margin-left: 10%;
  margin-right: 10%;
}

.background {
  background-color: rgba(146, 180, 191, 0.781);
  margin: 5% 20%;
  border-radius: 20px;
}

.texto-criadores {
  display: flex;
  padding: 5%;
}

.logos {
  display: flex;
  flex-wrap: wrap;
  margin: 0px 10% 0 0%;
}

.bio-equipe p {
  font-size: 1.4em;
}
